.body-container{
    font-family: 'Inter' !important;
    background-color: white;
    padding-bottom: 15rem;
}

.input-form{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto;
    padding: 1.2rem;
    border-radius: .7rem;
    margin-top: 2rem;
    box-shadow: 0 3px 20px rgb(0 0 0 / 0.3);
}
#link-input{
    font-family: Ubuntu !important;
    height: 3rem;
    width: 33rem;
    font-size: 160%;
    text-align: center;
    border: 2px solid #005bbc;
    border-radius: .7rem;
    outline: none;
    color: gray;
    margin-bottom: 1rem;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

}

#input-submit{
    background-color: #005bbc;
    text-align: center;
    padding: 1.1rem;
    border-radius: .7rem;
    cursor: pointer;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding-top: 20px;
    padding-bottom: 14px;
    /* box-shadow: 0 3px 10px rgb(0 0 0 / 0.4); */
}


#download-logo{
    color: #ffd700;
}

.invalid-link{
    border: 2px solid rgb(255, 0, 0) !important;
    box-shadow: 0 4px 20px rgb(255 0 0 / 0.5);
}

.valid-link{
    border: 2px solid rgb(0, 240, 0) !important;
    box-shadow: 0 4px 20px rgb(0 240 0 / 0.5);
}

.regular-border{
    border: 2px solid #005bbc !important;
}
#download-buttons{
    border-radius: .5rem;
    width: 100%;
    border:none;
    margin-top: 1rem;
    margin: auto;
}
.video-container{
    margin: auto;
    padding: 1rem;
    box-shadow: 0 3px 20px rgb(0 0 0 / 0.3);
    border-radius: 1rem;
    margin-top: 2rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.ErrorPopup{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(90,90,90,0.5);

    
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-container{
    padding: 32px;
    width: 100%;
    height: 150px;
    max-width: 400px;
    background-color: white;
    border-radius: .5rem;
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.4);
}

#popup-close{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    width: 100%;
    margin-top: 1rem;
    padding: .8rem;
    font-size: 120%;
    font-weight: bold;
    background-color: white;
    cursor: pointer;
    border-radius:.7rem;;
    transition: 0.4s all ease;
    border: 0;
}

#popup-close:hover{
    box-shadow: 0 3px 20px rgb(0 0 0 / 0.4);
    transition: border-color .20s ease-out; 
    
}
#popup-header{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto;
}
#popup-text{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto;
    padding: 1rem;
}
.header-container{
    text-align: center;
    background-color: #005bbc;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.3);
    font-family: Ubuntu !important;
    display: flex;
}
#header-brand{
    padding: 1rem;
    width: 13rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin:auto;
    margin-right: 0;
}

.header-menu{
    margin: auto;
    font-size: 105%;
    font-weight: bold;
    margin: auto;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

}

.header-button{
    padding: .8rem;
    margin: .5rem;
    margin-bottom: 0;
    text-align: center;
    color: white;
    cursor: pointer;
    /* outline: 1px solid transparent; */
    border-radius: .7rem;
    transition: 0.4s all ease;
    text-decoration: none;
}

.header-button:hover{
    /* outline: 1px #016fe6 solid; */
    box-shadow: 0 3px 20px rgb(0 0 0 / 0.4);
    transition: outline-color .20s ease-out; 
}
.footer-container{
    display: flex;
    text-align: center;
    background-color: #005bbc;
    padding: 4rem;
    margin-top: 20rem;
  }

  .footer-menu{
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
      margin: auto;
  }
  
  .footer-container .footer-link{
    padding: 1rem;
    color: white;
    font-size: 110%;
    font-weight: bold;
  }


